.b-icon {
    /* In case there is text content in the element */
    display : inline-flex;
    gap     : .5em;
}

.b-icon::before,
.b-fw-icon::before {
    font-style   : normal;
    font-variant : normal;
    line-height  : 1;
    display      : grid;
    place-items  : center;

    /* Font Awesome not built in, include it in your app if you want our default icons */
    font-family  : var(--b-widget-icon-font-family);
    font-weight  : var(--b-widget-icon-font-weight, 900);
    /* Convenience rule to not have to add .fa also */
    content      : var(--fa);
}

.b-fw-icon::before {
    width : 1em;
}

