/* region Live clock icon */

.b-icon-clock-live {
    background-color : currentColor;
    border-radius    : 50%;
    width            : 1em;
    height           : 1em;
    animation-delay  : 0s;
    display          : grid;

    &:before,
    &:after {
        grid-area     : 1 / 1 / 1 / 1;
        position      : relative;
        font-size     : 1em !important;
        content       : "";
        width         : .10em !important;
        left          : .44em; /* half width of clock - half width of hand */
        background    : #fff;
        border-radius : .5em;
    }

    &:before {
        top                  : .15em;
        /*need !important since i:before has fixed height when used in fields etc. need to override it*/
        height               : .4em !important; /* half the width/height of the clock */
        transform-origin     : .05em .35em;
        animation            : b-anim-rotate 6s infinite linear;
        animation-play-state : paused;
        animation-delay      : inherit;
    }

    &:after {
        top                  : .22em;
        height               : .33em; /* top + height for little hand must = top + height for big hand */
        transform-origin     : .05em .3em;
        animation            : b-anim-rotate 72s infinite linear; /* time should be big-hand time * 12 */
        animation-play-state : paused;
        animation-delay      : inherit;
    }
}

/* endregion */

